

class Tile {
    char = '.';
    mouse = false;
    constructor(x, y) {
        this.x = x;
        this.y = y;
        //if (x % 2 == 0)
        //this.char = ' ';
    }
    getChar() {
        return this.char;
    }
    setChar(c) {
        this.char = c;
    }
    mouseOn() {
        this.mouse = true;
    }
    mouseOff() {
        this.mouse = false;
    }
    isMouse() {
        return this.mouse;
    }
}

export default Tile;