class Entity {
    constructor(x, y, h, w) {
        this.x = x;
        this.y = y;
        this.w = w;
        this.h = h;
    }

    getX() { return this.x }
    getY() { return this.y }
    getW() { return this.w }
    getH() { return this.h }
}

class Rect extends Entity {
    corner = 'O';
    verticalSide = '|';
    horizontalSide = '-';
    currentFrame = 0;

    getCorner() { return this.corner }
    getVS() { return this.verticalSide }
    getHS() { return this.horizontalSide }






    render(grid) {
        grid.getTile(this.x, this.y).setChar(this.corner);
        grid.getTile(this.x + this.w - 1, this.y).setChar(this.corner);
        grid.getTile(this.x, this.y + this.h - 1).setChar(this.corner);
        grid.getTile(this.x + this.w - 1, this.y + this.h - 1).setChar(this.corner);
        for (let i = 1; i < this.w - 1; i++) {
            grid.getTile(this.x + i, this.y).setChar(this.horizontalSide);
            grid.getTile(this.x + i, this.y + this.h - 1).setChar(this.horizontalSide);
        }
        for (let i = 1; i < this.h - 1; i++) {
            grid.getTile(this.x, this.y + i).setChar(this.verticalSide);
            grid.getTile(this.x + this.w - 1, this.y + i).setChar(this.verticalSide);
        }
    }


}




export default Rect;