//const tile = require("./tile");
import tile from "./tile";
import rect from "./asset";

class Grid {
    tiles = [];
    entities = [];
    constructor(ctx, w, h) {
        this.x = Math.floor(w / (23 * 0.52));
        this.y = Math.floor(h / 23);
        this.screenWidth = w;
        this.screenHeight = h;
        this.constructTiles();
        this.entities.push(new rect(0, 0, 12, 20));
        this.ctx = ctx;
    }
    update(w, h, mx, my) { //try to make more efficient
        let a = false;
        let b = false;
        if (w != this.screenWidth) {
            this.x = Math.floor(w / (23 * 0.52));
            this.screenWidth = w;
            a = true;
        }
        if (h != this.screenHeight) {
            this.y = Math.floor(h / 23);
            this.screenHeight = h;
            b = true;
        }
        if (a || b) {
            this.constructTiles();
        }


        this.renderEntities();
        this.drawScreen();

        let closestX = Math.floor(mx / this.screenWidth * this.x);
        let closestY = Math.floor(my / this.screenHeight * this.y);

        this.ctx.fillText("█", closestX * 12, 20 + closestY * 23);

        /*
        this.ctx.clearRect(0, 0, this.screenWidth, this.screenHeight);
        this.ctx.font = "20px Courier New";
        this.ctx.fillStyle = "rgba(0, 0, 0, 0.5)";
        this.ctx.fillText("█", 0, 20);
        //████
        this.ctx.fillText("█", 0, 43);
        this.ctx.fillText("█", 0, 66);
        this.ctx.fillText("█", 0, 89);
        this.ctx.fillText("█", 12, 20);
        //████
        this.ctx.fillText("█", 12, 43);
        this.ctx.fillText("█", 12, 66);
        this.ctx.fillText("█", 12, 89);

        */


    }
    renderEntities() {
        this.entities.forEach(e => e.render(this));
    }
    drawScreen() {
        this.ctx.clearRect(0, 0, this.screenWidth, this.screenHeight);
        this.ctx.font = "20px Courier New";
        this.ctx.fillStyle = "rgba(255, 255, 255, 1)";
        for (let j = 0; j < this.y; j++) {
            for (let i = 0; i < this.x; i++) {
                this.ctx.fillText(this.getTile(i, j).getChar(), i * 12, 20 + j * 23);
            }
        }
    }
    constructTiles() {
        this.tiles = [];
        for (let j = 0; j < this.y; j++)
            for (let i = 0; i < this.x; i++)
                this.tiles.push(new tile(i, j));
        //console.log(this.tiles);
    }
    getTile(x, y) {
        return this.tiles[this.x * y + x];
    }
    /*getString() {
        let string = '<p>';
        for (let j = 0; j < this.y; j++) {
            for (let i = 0; i < this.x; i++) {
                string += `<span `;
                if (this.getTile(i, j).isMouse())
                    string += `class="highlighted" `;
                string += `v-on:mouseover.stop="mouseover($event,${i},${j})">`
                string += this.getTile(i, j).getChar();
                string += `</span>`
            }
            string += `<br>`
        }
        string += `</p>`;
        return string;
    }*/
    getData() {
        return {
            x: this.x,
            y: this.y
        }
    }
}

export default Grid;